import { RenderNodeStyleProps, Controller, Block } from '@ali/4ever-cangjie';
import { SpacingUtil, defaultCompactSpacing } from '@ali/4ever-utils';

export default function getNodeStyle(
  props: RenderNodeStyleProps,
  controller: Controller,
  next: Function,
) {
  const { node, docStyle } = props;
  if (!Block.isBlock(node) || !node.data.list) return next();

  let style: React.CSSProperties = {};
  const { pPr } = docStyle?.docDefaults?.data || {};

  // 段间距
  style = {
    ...SpacingUtil.toStyle(node, pPr?.spacing ? defaultCompactSpacing : undefined),
  };

  const { lineHeight, ...rest } = style;
  return rest;
}
